html {
  background: #eeeeee;
  color: #454545;
}

@media (prefers-color-scheme: dark) {
    html {
      color: #ddd;
      background-color: #222;
    }
  }

body {
  margin: 40px auto;
  max-width: 650px;
  line-height: 1.6;
  font-size: 18px;
  padding: 0 10px;
}
h1,
h2,
h3 {
  line-height: 1.2;
}
